import axios from 'axios'
import qs from 'qs'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

axios.defaults.timeout = 5000 // 
axios.defaults.baseURL = 'http://pay.loyom.com' // 
// process.env.BASE_URL
/** **** axios ******/
const service = axios.create({
  baseURL: 'http://pay.loyom.com', // apibase_url
  timeout: 5000 // 
})
/** **** request==> ******/
service.interceptors.request.use(config => {
  // app.$vux.loading.show({
  //     text: ''
  // });

  (config.method === 'post')?(config.data = qs.stringify({ ...config.data })):(config.params={...config.params})
  config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
  return config
}, error => { // 
  ElementUI.Message({
    message: error,
    type: 'error'
  })
  Promise.reject(error)
})

/** **** respone==> ******/
service.interceptors.response.use(
  response => { // 
    // 
    if (response.data.code === 200) {
      return response
    } else {
      ElementUI.Message({
        message: response.data.message,
        type: 'error'
      })
    }
  },
  error => { // 
    const text = JSON.parse(JSON.stringify(error)).response.status === 404 ?
      '404' :
      ''
    ElementUI.Message({
      message: '404',
      type: 'error'
    })
    return Promise.reject(error)
  }
)

export default service



