<template>
  <div>
    <div class="breadcrumbs">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="contents">
      <el-row>
        <el-button size="mini" type="success" @click="add"> +</el-button>
      </el-row>
      <div style="display: flex;margin-top: 10px;">
        <el-row :gutter="20">
          <el-col :span="16">
            <el-input placeholder="" v-model="searchKey" size="mini" clearable></el-input>
          </el-col>
          <el-col :span="2">
            <el-button size="mini" type="success" @click="search"></el-button>
          </el-col>
        </el-row>

      </div>

      <el-table :data="tableData" border :stripe="true" size="mini" empty-text style="width: 100%;margin-top: 20px;">
        <el-table-column fixed type="index" label="" width="60"></el-table-column>
        <el-table-column fixed prop="resId" label="id"></el-table-column>
        <el-table-column fixed prop="intro" label=""></el-table-column>
        <el-table-column fixed prop="url" label=""></el-table-column>
        <el-table-column fixed label="">
          <template slot-scope="scope">
            <img :src="scope.row.filePath" style="height: 30px; width: 30px;border-radius: 50%;" />
          </template>
        </el-table-column>
        <el-table-column width="200">
          <template slot-scope="scope">

            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align:center;padding-top: 20px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="1"
          :page-size="10" layout="total, prev, pager, next, jumper" :total="total"></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        searchKey: '',
        currentPage4: 1,
        tableData: [],
        options: [],
        value: '',
        total: 0,
        pageNum: 0,
        pageSize: 15
      };
    },
    methods: {
      search() {

      },
      getStore() {
        var _this = this;
        var data = {
          pageNum: _this.pageNum,
          pageSize: _this.pageSize
        };

        this.axios
          .post('/api/v1/banner/query/page', data)
          .then(function(response) {
            if (response.data.code === 200) {
              _this.tableData = response.data.data.datas;
              _this.total = response.data.data.totalCount;
            }
          })
      },
      handleCurrentChange(val) {
        this.pageNum = `${val}` - 1
        this.getStore();
      },
      handleSizeChange(val) {
        this.pageSize = `${val}`
        this.getStore();
      },


      handleDelete(index, row) {
        let _this = this;
        this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning',
            center: true
          })
          .then(() => {
            let data = {
              id: row.row.id
            }
            _this.axios
              .post('/api/v1/banner/delete', data)
              .then(function(response) {
                console.log(response.data.code === 200);
                if (response.data.code === 200) {
                  this.$message({
                    type: 'success',
                    message: '!'
                  });
                  _this.getStore();
                } else {
                  this.$message({
                    type: 'warn',
                    message: ''
                  });
                }
              })
              .catch(function(error) {
                this.$message({
                  type: 'info',
                  message: ''
                });
              });

          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
      },
      add() {
        this.$router.push({
          path: '/bannerAdd'
        });
      }
    },
    mounted: function() {
      this.getStore();
    }
  };
</script>

<style scoped="scoped">

  #operations {
    text-align: right;
  }
</style>



