<template>
	<div><div id="pieChart" style="width: 100%; height: 300px"></div></div>
</template>
<script>
export default {
	data() {
		return {
		};
	},
	mounted() {
		this.pieChart();
	},
	created: function() {
		let height = document.body.clientHeight;
	},
	methods: {
		pieChart() {
			let myChart = this.$echarts.init(document.getElementById('pieChart'));
			var option = {
				tooltip: {
					trigger: 'item',
					formatter: '{a} <br/>{b}: {c} ({d}%)'
				},
				legend: {
					orient: 'vertical',
					x: 'left',
					data: ['', '', '', '', '', '', '', '', '', '']
				},
				series: [
					{
						name: '',
						type: 'pie',
						selectedMode: 'single',
						radius: [0, '30%'],

						label: {
							normal: {
								position: 'inner'
							}
						},
						labelLine: {
							normal: {
								show: false
							}
						},
						data: [{ value: 335, name: '', selected: true }, { value: 679, name: '' }, { value: 1548, name: '' }]
					},
					{
						name: '',
						type: 'pie',
						radius: ['40%', '55%'],

						data: [
							{ value: 335, name: '' },
							{ value: 310, name: '' },
							{ value: 234, name: '' },
							{ value: 135, name: '' },
							{ value: 1048, name: '' },
							{ value: 251, name: '' },
							{ value: 147, name: '' },
							{ value: 102, name: '' }
						]
					}
				]
			};
			myChart.setOption(option);
		}
	}
};
</script>

<style></style>



